::-webkit-input-placeholder {
  color: #f78a9e;
}

input[type="search"] {
  margin: 10px 0;
  -webkit-appearance: none;
  -webkit-transition: -webkit-box-shadow 300ms ease;
  transition: -webkit-box-shadow 300ms ease;
  -o-transition: box-shadow 300ms ease;
  transition: box-shadow 300ms ease;
  transition: box-shadow 300ms ease, -webkit-box-shadow 300ms ease;
  color: #f43356;
  border: 0px;
  border-radius: 6px;
  width: 300px;
  height: 45px;
  padding: 14px;
  font-size: 22px;
}

.light input[type="search"] {
  background-color: #fafafa;
  -webkit-box-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
          box-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
}

.dark input[type="search"] {
  background-color: #131a20;
  -webkit-box-shadow: 0px 1px 2px 0px rgba(255,255,255,0.2);
  box-shadow: 0px 1px 2px 0px rgba(255,255,255,0.2);
}

input[type="search"]:focus {
  outline: 0;
}

@media screen and (max-width: 680px) {
  input[type="search"] {
    width: 85%;
  }
}



