body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

*,
*::before,
*::after {
  -webkit-tap-highlight-color: transparent;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  text-decoration: none;
}

.secondary a {
  color: #f43356;
}

h1 {
  text-shadow: 0 1px 0 #e02144, 0 2px 0 #db1a3d, 0 3px 0 #d11437, 0 4px 0 #c60d30, 0 5px 0 #ba092a, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
  margin: 0;
  color: #f43356;
  font-size: 130px;
}

h2 {
  color: #f78a9e;
  font-size: 30px;
}

.secondary h2 {
  margin-top: 0px;
  margin-bottom: 10px;
  color: #f43356;
}

@-webkit-keyframes fade-in {
  0% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
    opacity: 0;
  }

  50% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
    opacity: 0;
  }

  50% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

.secondary h3 {
  margin: 10px auto;
}

.primary img {
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 400ms;
          animation-duration: 400ms;
  border: 3px solid #fff;
  border-radius: 6px;
  -webkit-box-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
          box-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
  width: calc(100% - 60px);
}

.primary > div {
  position: -webkit-sticky;
  position: sticky;
  top: 12px;
}

h3 {
  text-align: left;
  color: #f43356;
}

.title {
