  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: calc(100vh - 140px);
}

.info {
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 400ms;
          animation-duration: 400ms;
  padding: 5px;
  margin: 20px auto;
  width: calc(100% - 60px);
  background-color: #fff;
  border-radius: 6px;
  -webkit-box-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
          box-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
}

.primary {
  width: calc(40% - 10px);
}

.secondary {
  text-align: left;
  width: calc(60% - 10px);
}

.page {
  padding-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
}

.content {
  position: relative;
  width: calc(100% - 115px);
}

.artwork {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100px;
}

.artwork img {
  margin-right: 15px;
  width: 100px;
  height: auto;
  border-radius: 2px;
}

.light .artwork img {
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.34);
  box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.34);
}

.dark .artwork img {
  -webkit-box-shadow: 0px 0px 2px 0px rgba(255,255,255,0.34);
  box-shadow: 0px 0px 2px 0px rgba(255,255,255,0.34);
