}

span.explicit {
  position: absolute;
  margin-left: 7px;
  margin-top: -4px;
}

.explicit::before {
  content: 'Explicit';
  background-color: #f78a9e;
  padding: 3px 4px;
  border-radius: 2px;
  font-size: 9px;
}

span.explicit::before {
  padding: 4px 7px;
  font-size: 15px;
}

.light .explicit::before {
  color: #fff;
}

.dark .explicit::before {
  color: #21272D;
}

.song-title {
  color: #f43356;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-size: 18px;
  width: 100%;
}

.album {
  color: #f78a9e;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 100%;
  font-size: 14px;
  margin: 4px 0;
}

.author {
  color: #f43356;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 100%;
  font-weight: 700;
  font-size: 14px;
  margin: 4px 0;
}



